<template>
  <div class="activity">
    <el-card class="search">
      <el-form :model="searchForm" ref="searchForm">
        <el-select class="search-item" clearable v-model="searchForm.cdxOne" placeholder="">
          <el-option
            v-for="item in actState"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-input class="search-item" v-model="searchForm.cdxTwo" placeholder="ID"></el-input>
        <el-input class="search-item" v-model="searchForm.cdxThree" placeholder="|"></el-input>
        <el-button type="primary" icon="el-icon-search" @click="searchSubmit"></el-button>
      </el-form>
    </el-card>
    <el-card class="activity-list">
      <div slot="header" class="card-head">
        <span class="title" style="float:left"></span>
        <el-button style="float: right;font-weight:400" type="primary" @click="changeTarget">+ </el-button>
      </div>
      <div class="list">
        <!--  -->
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column fixed prop="id" label="ID" width="80"></el-table-column>
          <el-table-column label="" width="220">
            <template slot-scope="scope">
              <div class="tip" style="display:none" :ref="'tipDOM_'+scope.$index">
                <vue-qr :text="scope.row.sao_ma_Activity_url" :size="150"></vue-qr>
              </div>
              <el-popover
                placement="top-start"
                width="150"
                trigger="hover"
                @show="tipShow(scope.$index)"
              >
                <div class="codeBox" slot>
                  <div class="code" v-html="newTipDOM"></div>
                </div>
                <span
                  slot="reference"
                  style="color:#00aaed;cursor: pointer;"
                >{{scope.row.activityTitle}}</span>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <div class="act-state">
                <span
                  class="round"
                  style="display:inline-block;width:5px;height:5px;background-color:#ffeb3b;border-radius:50%;"
                  v-if="scope.row.auditStatus=='1'"
                ></span>
                <span
                  class="round"
                  style="display:inline-block;width:5px;height:5px;background-color:#67c23a;border-radius:50%;"
                  v-else-if="scope.row.auditStatus=='2'"
                ></span>
                <span
                  class="round"
                  style="display:inline-block;width:5px;height:5px;background-color:#f56c6c;border-radius:50%;"
                  v-else-if="scope.row.auditStatus=='3'"
                ></span>
                <span
                  class="round"
                  style="display:inline-block;width:5px;height:5px;background-color:#66b1ff;border-radius:50%;"
                  v-else-if="scope.row.auditStatus=='4'"
                ></span>
                <span
                  class="round"
                  style="display:inline-block;width:5px;height:5px;background-color:#999;border-radius:50%;"
                  v-else
                ></span>
                <span v-if="scope.row.auditStatus=='1'"></span>
                <span v-else-if="scope.row.auditStatus=='2'"></span>
                <span v-else-if="scope.row.auditStatus=='3'"></span>
                <span v-else-if="scope.row.auditStatus=='4'"></span>
                <span v-else></span>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="openActivity" label="" width="80">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.auditStatus=='2'?actSwitch=true:actSwitch=false"
                disabled
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="" width="80">
            <template slot-scope="scope">
              <span v-if="scope.row.pattern=='1'"></span>
              <span v-else></span>
            </template>
          </el-table-column>
          <el-table-column label="" width="220">
            <template slot-scope="scope">
              <div
                class="actData"
                v-if="scope.row.viewsNum!=null||scope.row.shareVolume!=null||scope.row.zan!=null"
              >
                <div v-if="scope.row.viewsNum!=null">{{scope.row.viewsNum}}</div>
                <div v-else>0</div>
                <div v-if="scope.row.shareVolume!=null">{{scope.row.shareVolume}}</div>
                <div v-else>0</div>
                <div v-if="scope.row.zan!=null">{{scope.row.zan}}</div>
                <div v-else>0</div>
              </div>
              <div v-else></div>
            </template>
          </el-table-column>
          <el-table-column prop="reasonsForFailureOfAudit" label=""></el-table-column>
          <el-table-column label="/" width="150">
            <template slot-scope="scope">
              <span v-if="scope.row.usersVO.region=='1'"></span>
              <span v-else>{{scope.row.usersVO.region_province_city}}</span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope" width="200">
              <span>{{scope.row.usersVO.usersName}}</span>
              <span v-if="scope.row.usersVO.role=='1'"></span>
              <span v-else-if="scope.row.usersVO.role=='2'"></span>
              <span v-else-if="scope.row.usersVO.role=='3'"></span>
              <span v-else-if="scope.row.usersVO.role=='4'"></span>
              <span v-else></span>
              <span>{{scope.row.createTimeName}}</span>
            </template>
          </el-table-column>
          <el-table-column fixed="right" label="" width="270">
            <template slot-scope="scope">
              <div class="btn-box" v-if="scope.row.auditStatus=='1'" style="text-align: left;">
                <div v-if="role=='1'">
                  <el-button
                    size="small"
                    type="success"
                    @click="changeActState(scope.row.id,'2',scope.row.reasonsForFailureOfAudit)"
                  ></el-button>
                  <el-button size="small" type="primary" @click="editor(scope.row.id)"></el-button>
                  <el-button size="small" @click="copyActivity(scope.row.id)"></el-button>
                  <el-button size="small"></el-button>
                  <el-button size="small" @click="rejected(scope.row.id)"></el-button>
                  <el-button size="small" @click="teacherSearch(scope.row.id)"></el-button>
                </div>
                <div v-else-if="uid==scope.row.followers">
                  <el-button
                    size="small"
                    type="success"
                    @click="changeActState(scope.row.id,'2',scope.row.reasonsForFailureOfAudit)"
                  ></el-button>
                  <el-button size="small" type="primary" @click="editor(scope.row.id)"></el-button>
                  <el-button size="small" @click="copyActivity(scope.row.id)"></el-button>
                  <el-button size="small"></el-button>
                  <el-button size="small" @click="rejected(scope.row.id)"></el-button>
                  <el-button size="small" @click="teacherSearch(scope.row.id)"></el-button>
                </div>
                <div v-else></div>
              </div>
              <div v-else-if="scope.row.auditStatus=='2'">
                <el-button
                  size="small"
                  @click="changeActState(scope.row.id,'4',scope.row.reasonsForFailureOfAudit)"
                ></el-button>
                <el-button size="small" @click="copyActivity(scope.row.id)"></el-button>
              </div>
              <div class="btn-box" v-else-if="scope.row.auditStatus=='4'" style="text-align: left;">
                <el-button
                  size="small"
                  type="success"
                  @click="changeActState(scope.row.id,'2',scope.row.reasonsForFailureOfAudit)"
                ></el-button>
                <el-button size="small" type="primary" @click="editor(scope.row.id)"></el-button>
                <el-button size="small" @click="copyActivity(scope.row.id)"></el-button>
                <el-button size="small"></el-button>
                <el-button size="small" @click="rejected(scope.row.id)"></el-button>
                <el-button size="small" @click="teacherSearch(scope.row.id)"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5, 10]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </el-card>
    <div class="activity-prompt"></div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="rejAct"
      :modal-append-to-body="false"
      top="7vh"
      width="40%"
      center
    >
      <el-input
        type="textarea"
        :rows="5"
        placeholder=""
        maxlength="50"
        :show-word-limit="true"
        v-model="textarea"
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="rejAct = false"> </el-button>
        <el-button type="primary" @click="rjtDtm"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="teacherPopupAct"
      width="42%"
      center
      :modal-append-to-body="false"
    >
      <el-form :model="teacherFrom" label-width="100px">
        <div class="dialog-content">
          <el-form-item label="">
            <el-select v-model="teacherFrom.teacherSelItem" placeholder="">
              <el-option
                v-for="(item) in teacherListItems"
                :key="item.id"
                :label="item.advertisementName"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <template v-if="this.teacherId==''">
          <el-button type="primary" @click="setTeacher"></el-button>
          <el-button @click="teacherPopupAct = false"> </el-button>
        </template>
        <template v-else>
          <el-button type="primary" @click="changeTeacher"></el-button>
          <el-button @click="teacherPopupAct = false"> </el-button>
        </template>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import vueQr from "vue-qr";
import Events from "../../common/js/events";
export default {
  components: {
    vueQr,
  },
  inject: ["reload"],
  data() {
    return {
      actState: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "3",
          label: "",
        },
        {
          value: "4",
          label: "",
        },
        {
          value: "5",
          label: "",
        },
      ],
      searchForm: {
        cdxOne: "",
        cdxTwo: "",
        cdxThree: "",
      },
      // 
      tableData: [],
      page: "1",
      pageSize: "5",
      total: 0,
      disabled: false,
      actSwitch: true,
      code: "",
      uid: "",
      role: "",
      //
      teacherPopupAct: false,
      teacherListItems: [],
      teacherFrom: {
        teacherSelItem: "",
      },
      getActId: "",
      teacherId: "",
      // 
      rejAct: false,
      actId: "",
      textarea: "",
      //
      newTipDOM: "",
    };
  },
  created() {
    this.getActData();
    this.teacherList();
  },
  methods: {
    // 
    getActData: function () {
      this.uid = localStorage.getItem("uid");
      this.role = localStorage.getItem("role");
      const params = new URLSearchParams();
      params.append("uid", localStorage.getItem("uid"));
      params.append("page", this.page);
      params.append("show_num", this.pageSize);
      this.$http
        .post(this.$api.actList.actList, params, true)
        .then((result) => {
          if (result.data.state == "success") {
            this.total = result.data.count;
            this.tableData = result.data.getActivityVOList;
            this.code = result.data.getActivityVOList;
          }
        });
    },
    // 
    copyActivity: function (id) {
      localStorage.setItem("activityId", id);
    },
    teacherList: function () {
      const params = new URLSearchParams();
      params.append("uid", localStorage.getItem("uid"));
      this.$http
        .post(this.$api.teacherSelList.teacherSelList, params, true)
        .then((result) => {
          console.log("", result);
          if (result.data.state == "success") {
            this.teacherListItems = result.data.getAdvertisementVOList;
          }
        });
    },
    teacherSearch: function (actId) {
      this.getActId = actId;
      this.teacherPopupAct = true;
      const params = new URLSearchParams();
      params.append("ids", actId);
      this.$http
        .post(this.$api.actTeacherSearch.actTeacherSearch, params, true)
        .then((result) => {
          if (
            result.data.state == "success" &&
            result.data.activityAdvertisementVO != null
          ) {
            this.teacherId = result.data.activityAdvertisementVO.id;
            this.teacherFrom.teacherSelItem =
              result.data.activityAdvertisementVO.advertisementName;
          }
        });
      this.teacherFrom.teacherSelItem = "";
    },
    setTeacher: function () {
      const params = new URLSearchParams();
      params.append("uid", localStorage.getItem("uid"));
      params.append("activity_id", this.getActId);
      params.append("h_id", this.teacherFrom.teacherSelItem);
      this.$http
        .post(this.$api.addTeacher.addTeacher, params, true)
        .then((result) => {
          if (result.data.state == "success") {
            Events.errPrompt(
              this.$message({
                message: "",
                type: "success",
              })
            );
          }
        });
      this.teacherFrom.teacherSelItem = "";
      this.teacherPopupAct = false;
    },
    changeTeacher: function () {
      const params = new URLSearchParams();
      params.append("id", this.teacherId);
      params.append("h_id", this.teacherFrom.teacherSelItem);
      this.$http
        .post(this.$api.changeTeacher.changeTeacher, params, true)
        .then((result) => {
          if (result.data.state == "success") {
            Events.errPrompt(
              this.$message({
                message: "",
                type: "success",
              })
            );
          }
        });
      this.teacherFrom.teacherSelItem = "";
      this.teacherPopupAct = false;
    },
    changeActState: function (actId, actState, actWhy) {
      const params = new URLSearchParams();
      params.append("uid", localStorage.getItem("uid"));
      params.append("id", actId);
      params.append("auditStatus", actState);
      if (actState == "3") {
        params.append("reasonsForFailureOfAudit", actWhy);
      }
      this.$http
        .post(this.$api.changeActState.changeActState, params, true)
        .then((result) => {
          this.getActData();
        });
      this.teacherFrom.teacherSelItem == "";
      this.teacherPopupAct = false;
    },
    editor: function (actId) {
      this.$router.push({
        path: "/act/activity/editor",
        query: {
          id: actId,
        },
      });
    },
    searchSubmit: function () {
      const params = new URLSearchParams();
      params.append("uid", localStorage.getItem("uid"));
      params.append("page", this.page);
      params.append("show_num", this.pageSize);
      params.append("auditStatus", this.searchForm.cdxOne);
      params.append("id_activityTitle_shareTitle", this.searchForm.cdxTwo);
      params.append("usersName_remarks", this.searchForm.cdxThree);
      this.$http
        .post(this.$api.actList.actList, params, true)
        .then((result) => {
          if (result.data.state == "success") {
            if (result.data.count != 0) {
              this.total = result.data.count;
              this.tableData = result.data.getActivityVOList;
              this.code = result.data.getActivityVOList;
            } else {
              Events.errPrompt(
                this.$message({
                  showClose: true,
                  message: "",
                  type: "error",
                })
              );
            }
          } else {
            Events.errPrompt(
              this.$message({
                showClose: true,
                message: "",
                type: "error",
              })
            );
          }
        });
    },
    changeTarget: function () {
      this.$router.push({
        path: "/act/activity/create",
      });
    },
    tipShow: function (index) {
      this.newTipDOM = this.$refs["tipDOM_" + index].innerHTML;
    },
    rejected: function (actId) {
      this.actId = actId;
      this.rejAct = !this.rejAct;
    },
    rjtDtm: function () {
      const params = new URLSearchParams();
      params.append("uid", localStorage.getItem("uid"));
      params.append("id", this.actId);
      params.append("auditStatus", "3");
      if (this.textarea == "") {
        this.$message.error("");
        return;
      } else {
        params.append("reasonsForFailureOfAudit", this.textarea);
      }
      this.$http
        .post(this.$api.rejected.rejected, params, true)
        .then((result) => {
          if (result.data.state == "success") {
            this.textarea = "";
            this.$message({
              message: ",",
              type: "success",
            });
            this.rejAct = !this.rejAct;
            this.getActData();
          }
        });
    },
    removeRejAct: function () {
      this.rejAct = !this.rejAct;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getActData();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getActData();
    },
  },
};
</script>

<style scoped>
.activity .search {
  width: 100%;
  margin-bottom: 20px;
}
.activity .search .el-card__body {
  overflow: hidden;
}
.activity .search .search-item {
  width: 230px;
  display: block;
  float: left;
  margin-right: 20px;
}
/*  */
.activity-list .card-head {
  overflow: hidden;
  line-height: 40px;
}
.activity-list .card-head span.title {
  display: block;
  float: left;
  color: #303133;
}
.activity .activity-prompt {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 0;
  font-size: 12px;
  color: #ff8484;
}
/*  */
.el-table_1_column_2 .el-button {
  border: none;
  background-color: transparent;
  color: #00aaed;
}
.el-table_1_column_2 .el-button:hover {
  background-color: #dcdfe6;
}
.activity .list .code-warpper {
  position: relative;
}
</style>


